/* EDIT PROFILE */
#edit_profile{
	padding: 10px 20px;
	.grdprofile{
		display: grid;
		grid-template-columns: 1fr 350px;
		grid-template-rows: 1fr 1fr;
		grid-template-areas:
			"content_left avatar"
			"content_left preview_resume";
		
		.content_left{
			grid-area: content_left;

			#edtprof_accrdn{
				width: 900px;

				.card{
					border: 0;
					border-radius: 0;

					.card-header{
						background-color: #8ad3ef;
						color: #FFF;
						cursor: pointer;
						border-radius: 0;
						padding: 8px 15px;
					}
				}
			}
		}
		
		.avatar{
			grid-area: avatar;
			text-align: center;

			.ttl{
				text-align: left;
			}

			.dsplaypc{
				    display: inline-block;
				    height: 160px;
				    overflow: hidden;
				
				.prvw{
					position: relative;
					left: 0; right: 0;
				}

				.fa {
			        font-size: 150px;
				    color: #CCC;
				    margin: 5px 0;
				}
			}

			.alert{
				padding: 5px 15px 10px;
				
				ul{
				    margin: 0 0 0 15px;
				    padding: 0;
				    text-align: left;
				    font-size: 14px;
				}
			}

			.fileUpload{
		        position: relative;
			    overflow: hidden;
			    margin: 5px 0;
			    background-color: #8ad3ef;
			    width: 80%;
			    text-transform: uppercase;
			    border: 1px solid #3bbaea;
			    padding: 5px 10px;

				.upload{
					position: absolute;
					top: 0;
					right: 0;
					margin: 0;
					padding: 0;
					font-size: 20px;
					cursor: pointer;
					opacity: 0;
					filter: alpha(opacity=0);
				}
			}

		}

		.preview_resume{
			grid-area: preview_resume;
		}

		.cntnbx{
			background-color: #FFF;
			padding: 10px 20px 20px;
			margin: 8px;
		}
	}
}
#cropModal{
	.modal-content{
		padding: 10px 20px;
		position: relative;
		
		.close{
		    position: absolute;
		    right: 20px;
		    top: 10px;
		    outline: none;
		    cursor: pointer;
		}

		.ttl{
			width: 100%;
		}
		.modal-body{
			min-height: 400px;

			.imgcropper{
				width: 100%;
				text-align: center;
				display: none;
				
				.jcrop-holder {
				    margin: 0 auto;
				    .jcrop-keymgr{visibility: hidden;}
				    div {
					    border-radius: 50%;
					    overflow: hidden;
					}
				}
			}
		}
	}
}

.prvw {
    position: absolute;
    z-index: 999;
    left: -10px;
    display: none;

    .preview-pane {
	    display: block;
	    position: relative;
	    padding: 6px;

	    .preview-container {
		    width: 150px;
		    position: relative;
		    height: 150px;
		    border-radius: 50%;
		    border: 5px inset #6dc5e6;
		    overflow: hidden;
		    text-align: center;
		}
	}
}